[class|='dimension'] {
	position: absolute;
}
[class|='dimension'] .ant-input-number {
	max-width: 60px;
}

.dimension-outside {
	top: 20%;
	right: 0%;
}
.dimension-innerside {
	top: 57%;
	right: 0%;
}
.dimension-angleA {
	top: 20%;
	right: 38%;
}
.dimension-angleB {
	top: 29%;
	right: 38%;
}
.dimension-legB {
	top: -2%;
	right: 50%;
}

.dimension-bevel {
	top: -25px;
	right: 25%;
}

.unknown-side {
	border-color: fuchsia;
	box-shadow: 0 0 0 2px rgb(255 24 163 / 14%);
}

@media (max-width: 450px) {
	[class|='dimension'] .ant-input-number {
		max-width: 55px;
	}
	.dimension-outside {
		top: 17%;
		right: -5%;
	}
	.dimension-innerside {
		top: 54%;
		right: -5%;
	}
	.dimension-angleA {
		top: 18%;
		right: 33%;
	}
	.dimension-angleB {
		top: 29%;
		right: 33%;
	}
	.dimension-legB {
		top: -5%;
		right: 48%;
	}
}
