.row-square :first-child {
	text-align: right;
}

.row-square .ant-table-cell:not(:first-child) {
	text-align: center;
	font-weight: bold;
	background-color: #f0f0f0;
	cursor: pointer;
}

.ant-table-cell.table-select {
	/* color: #fff !important;
	background-color: #33cce5 !important; */
	color: #fff;
	background: #1890ff !important;
	border-color: #1890ff;
	text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
	-webkit-box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
	box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
}

.ant-table table {
	border-spacing: 1px;
}
